<template>
  <div>

    <navbar @click-add-btn="showModal = true" />

    <!-- create new target - modal -->
    <modal v-if="showModal" @close="showModal = false">
      <h3 slot="header">New target</h3>
      <template v-slot:body>
        <form-crud 
          class=""
          @submit="addItem" 
          @cancel="showModal = false" 
        />
      </template>
    </modal>

    <!-- divider -->
    <h5 class="ui horizontal divider header" style="display: flex; margin: 10px 0px 20px 0px;">
      <i class="flag icon" style="font-size: 1.1rem;"></i>
      Targets
    </h5>

    <!-- items -->
    <div class="container">
      <div class="row">
        <div v-for="(item, i) in items" :key="i" class="col-12 col-sm-6 mb-4" >
          <card 
            v-bind="{ ...item, index: i }"
            :key="i+'c'"
            @change-item="changeItem" 
          />
        </div>

      </div>

    </div>

  </div>
</template>

<script>
// comps
import Navbar from '@/components/navbar.vue'
import Card from '@/components/card.vue'
import FormCrud from '@/components/form-crud.vue'
import Modal from '@/components/modal.vue'

export default {
  components: {
    Navbar,
    Card,
    FormCrud,
    Modal,
  },

  data: () => ({
    showModal: false,
    newItem: {
      title: '',
      target: 0,
      balance: 0
    },
    errors: [],
    items: [
      {title: 'Save up money', target: 10000, balance: 250},
      {title: 'Buy a car', target: 900, balance: 650},
    ]
  }),

  methods: {
    addItem(item) {
      this.items.push(item)
      this.$notify({
        message: 'Item was created',
        type: 'success'
      });
      this.showModal = false
    },

    changeItem(item) {
      let {index, ...other} = item

      for (let key of Object.keys(other)) {
        this.items[index][key] = other[key]
      }

       this.$notify({
        message: 'Item was changed',
        type: 'info'
      });
    }
  }
}
</script>

<style lang="scss" src="@/assets/index.scss"></style>
