<template>
<div>
  <div class="ui form">
    <div class="field">
      <label>Title</label>
      <input v-model="form.title" placeholder="Title..." type="text" />
    </div>
    <div class="row">
      <div class="col field">
        <label>Target</label>
        <input v-model.number="form.target" placeholder="Target..." type="text" />
      </div>
      <div class="col field">
        <label>Actual balance</label>
        <input  v-model.number="form.balance" value=" " placeholder="Actual balance..." type="text" />
      </div>
    </div>

     <div class="ui checkbox">
      <input type="checkbox" v-model="targetDone">
      <label>This target is done</label>
    </div>
  </div>

  <br />

  <!-- form - errors -->
  <div v-if="errors.length" class="ui red message tiny">
    <div class="ui bulleted list">
      <div v-for="(error, index) of errors" :key="index" class="item">{{ error }}</div>
    </div>
  </div>

  <button @click="submit" class="ui tiny submit button">Submit</button>
  <button @click="$emit('cancel')" class="ui tiny button">Cancel</button>
</div>
</template>

<script>
export default {
  props: {
    title: String, 
    target: Number, 
    balance: Number,
    editMode: {
      type: Boolean,
      default: false,
    }
  },

  data() {
    let {title, target, balance} = this
    return {
      errors: [],
      form: {
        title: title || '',
        target: target || '',
        balance: balance || ''
      },
      targetDone: false,
      prevStateBalance: 0,
    }
  },

  watch: {
    targetDone(value) {
      if (value) {
        this.prevStateBalance = this.form.balance
        this.form.balance = this.target 

      } else {
        this.form.balance = this.prevStateBalance
      }
    }
  },

  methods: {
    submit() {
      this.errors = []

      if (this.checkFields()){
        return
      }

      this.$emit('submit', this.form)

      if (!this.editMode) {
        this.form = {
          title: '',
          target: 0,
          balance: 0
        }
      }
    },

    checkFields() {
      let { title, balance, target } = this.form

      balance = Number(balance)
      target = Number(target) 

      console.log(balance, target)
      console.log(Number.isNaN(balance), Number.isNaN(target))
    
      if (title === '') {
        this.errors.push('Title is not filled')
      }

      if (Number.isNaN(balance) || Number.isNaN(target)) {
        this.errors.push('Balance or Target must be a number')
      }

      if (balance > target) {
        this.errors.push('Balance cant be bigger than target')
      }

      if (target == 0) {
        this.errors.push('Target cant be a zero')
      }

      return this.errors.length > 0 
    }
  }
}
</script>